@import "components/m_header";
@import "components/m_aside";
@import "components/m_footer";

@media screen and (max-width: 1200px), screen and (max-device-width: 1200px){
  body{
    .content{
      .container{
        .row{
          .col-md-9{
            .products{
              &__cart{
                .cart{
                  width: 32%;
                  ul{
                    .inCart{
                      width: 55%;
                    }
                  }
                  .hover{
                    width: 223px;
                    bottom: -133px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 992px), screen and (max-device-width: 992px){
  body{
    .content{
      .container{
        .row{
          .col-md-3{
            .aside{
              display: none;
            }
          }
          .col-sm-12{
            .products{
              &__cart{
                .cart{
                  ul{
                    .inCart{
                      width: 56%;
                    }
                  }
                  .hover{
                    width: 230px;
                    bottom: -183px;
                  }
                }
              }
              &__pagination{
                ul{
                  display: none;
                }
                .showMore{
                  display: block;
                }
              }
            }
            .aside{
              margin: 35px 0 0;
              display: flex;
            }
          }
        }
      }
    }
  }
}